footer {
  background-color: #1C1E27;
}

.contacts {
  margin-top: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;

  p {
    color: #ffffff;

    &:nth-child(1) {
      font-family: "TTCommons-DemiBold", sans-serif;
      font-size: calc(39px + 3vw);
      line-height: calc(63px + 3vw);
    }

    &:nth-child(2) {
      font-family: "TTCommons-Regular", sans-serif;
      font-size: calc(11px + 1vw);
      line-height: calc(19px + 1vw);
      text-align: center;
    }

    &.mail {
      font-family: "TTCommons-Bold", sans-serif;
      font-size: calc(17px + 1vw);
      line-height: calc(19px + 1vw);
      margin-top: 40px;
    }
  }
}

.number {
  background: #ffffff;
  border-radius: 10px;
  margin-top: 30px;

  a {
    font-family: "TTCommons-Bold", sans-serif;
    font-size: calc(17px + 1vw);
    line-height: calc(19px + 1vw);
    color: #1C1E27;
    padding: 16px 57px 11px 57px;
  }
}

.logos {
  margin-top: 30px;

  ul {
    display: flex;
    flex-direction: row;

    li:not(:nth-child(3)) {
      margin-right: 50px;
    }
  }

  img {
    max-width: 50px;
    height: auto;
  }
}

.rights {
  margin-top: 80px;
  margin-bottom: 20px;
  text-align: center;

  p {
    color: #ffffff;
    font-family: "TTCommons-Light", sans-serif;
    font-size: calc(9px + 0.5vw);
    line-height: calc(12px + 0.5vw);
  }
}

/*Media*/

@media (max-width: 1280px) {
  .logos img {
    max-width: calc(27px + 1vw);
  }

  .number a {
    padding: 16px 37px 11px 37px;
  }
}

@media (max-width: 768px) {
  .logos ul li:not(:nth-child(3)) {
    margin-right: 35px;
  }

  .number a {
    padding: 13px 27px 8px 27px;
  }
}