.skillsWrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-row-gap: 82px;
}

hr {
  margin-top: 102px;
  margin-bottom: 32px;
}

/*Media*/

@media (max-width: 1024px) {
  .skillsWrapper {
    grid-template-columns: repeat(2, 1fr);
    grid-row-gap: 82px;
    justify-items: center;
  }
}

@media (max-width: 440px) {
  .skillsWrapper {
    grid-template-columns: repeat(1, 1fr);
  }
}