.projectsWrapper p {
  color: #ffffff;
}

.projectsTitle {
  p {
    color: #ffffff;
  }

  text-align-last: end;
  font-family: "TTCommons-DemiBold", sans-serif;
  font-size: calc(39px + 3vw);
  line-height: calc(63px + 3vw);
  margin-top: 90px;
}

.projectsWrapper {
  display: grid;
  margin-top: 60px;
  margin-bottom: 90px;
  grid-template-columns: repeat(2, 1fr);
  grid-row-gap: 30px;
  grid-column-gap: 20px;
  justify-items: end;
}

/*Media*/

@media (max-width: 1024px) {
  .projectsWrapper {
    grid-template-columns: repeat(1, 1fr);
    justify-items: center;
  }

  .projectsTitle {
    text-align-last: center;
  }
}

@media (max-width: 425px) {
  .projectsWrapper {
    margin-top: 40px;
  }
}