.projectsItem {
  width: fit-content;
}

.projectsItemBcg {
  position: relative;

  img {
    border-radius: 20px;
    max-width: 100%;
    height: auto;
  }
}

.projectsItemProject {
  position: absolute;
  top: 32px;
  left: 47px;
  right: 47px;
  bottom: 32px;
}

.projectsItemText {
  p {
    margin-top: 20px;
    font-family: "TTCommons-Regular", sans-serif;
    font-size: calc(11px + 1vw);
    line-height: calc(13px + 1vw);
  }

  span {
    font-family: "TTCommons-DemiBoldItalic", sans-serif;
    font-size: calc(17px + 1vw);
    line-height: calc(13px + 1vw);
  }
}

/*Media*/

@media (max-width: 470px) {
  .projectsItemProject {
    top: 22px;
    left: 32px;
    right: 32px;
    bottom: 22px;
  }
}