.skillsTitle {
  p {
    color: #ffffff;
    font-family: "TTCommons-DemiBold", sans-serif;
    font-size: calc(39px + 3vw);
    line-height: calc(63px + 3vw);
  }

  margin-top: 90px;
  margin-bottom: 60px;
}

/*Media*/

@media (max-width: 1024px) {
  .skillsTitle {
    text-align: center;
  }
}

@media (max-width: 425px) {
  .skillsTitle {
    text-align: center;
    margin-bottom: 40px;
  }
}