.descriptionWrapper {
  display: grid;
  grid-template-areas: "fullName description";
  grid-template-columns: min-content 1fr;
  align-items: center;
  margin-top: 355px;

  p {
    color: #ffffff;
  }
}

.description {
  grid-area: description;
  width: calc(15vw + 230px);
  justify-self: end;

  p {
    &:nth-child(1) {
      font-family: "TTCommons-Bold", sans-serif;
      font-size: calc(17px + 1vw);
      line-height: calc(17px + 1vw);
    }

    &:nth-child(2) {
      font-family: "TTCommons-Regular", sans-serif;
      font-size: calc(11px + 0.5vw);
      line-height: calc(12.5px + 0.5vw);
      margin-top: 25px;
    }
  }
}

.fullName {
  grid-area: fullName;

  p {
    font-family: "TTCommons-Bold", sans-serif;
    font-size: calc(39px + 3vw);
    line-height: calc(62px + 3vw);
  }
}

/*Media*/

@media (max-width: 1280px) {
  .descriptionWrapper {
    margin-top: 290px;
  }
}

@media (max-width: 1046px) {
  .description {
    width: calc(15vw + 250px);
  }
}

@media (max-width: 1024px) {
  .descriptionWrapper {
    margin-top: 240px;
  }
}

@media (max-width: 828px) {
  .descriptionWrapper {
    grid-template-areas: ". fullName ." ". description .";
    grid-template-columns: auto min-content auto;
    align-items: center;
    text-align: center;
  }

  .description {
    margin-top: 35px;
  }

  .fullName p {
    line-height: calc(42px + 3vw);
  }

  .description p:nth-child(2) {
    margin-top: 10px;
  }
}

@media (max-width: 768px) {
  .descriptionWrapper {
    margin-top: 200px;
  }
}

@media (max-width: 425px) {
  .descriptionWrapper {
    margin-top: 170px;
  }
}