nav {
  padding-top: 15px;
  padding-bottom: 15px;
  position: fixed;
  width: 100%;
  z-index: 9999;
  background-color: #252734;
  transform: translateY(0%);
  transition: transform 200ms;
}

.hidden {
  transform: translateY(-100%);
  transition: transform 200ms;
}

.navigation {
  display: grid;
  grid-template-areas: "logos menu cross";
  grid-template-columns: minmax(max-content, 1fr) minmax(max-content, 1fr);
  align-items: center;
}

.cross {
  width: 20px;
  height: 12px;
  cursor: pointer;
  grid-area: cross;
  position: relative;
  display: none;

  &:before, &:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #ffffff;
  }

  &:before {
    top: 10%;
    transition: transform 150ms, top 150ms 150ms;
  }

  &:after {
    bottom: 10%;
    transition: transform 150ms, bottom 150ms 150ms;
  }
}

.active.cross {
  &:before {
    transition: top 150ms, transform 150ms 150ms;
    top: 50%;
    transform: translateY(-50%) rotateZ(-45deg);
  }

  &:after {
    transition: bottom 200ms, transform 150ms 150ms;
    bottom: 50%;
    transform: translateY(50%) rotateZ(45deg);
  }
}

/*Media*/

@media (max-width: 768px) {
  .cross {
    display: block;
    margin-right: -10px;
  }
}

@media (max-width: 767px) {
  .cross {
    margin-right: 30px;
  }
}