.logos {
  ul {
    display: flex;

    a {
      color: #ffffff;
    }
  }

  grid-area: logos;
  justify-self: start;

  li:not(:nth-last-child(1)) {
    margin-right: 30px;
  }

  img {
    max-width: 25px;
    height: auto;
  }
}

/*Media*/

@media (max-width: 768px) {
  .logos {
    img {
      max-width: 20px;
    }

    margin-left: -10px;
  }
}

@media (max-width: 767px) {
  .logos {
    margin-left: 30px;
  }
}