.skillsText {
  p {
    color: #ffffff;
    font-family: "TTCommons-Regular", sans-serif;
    font-size: calc(13px + 1vw);
    line-height: calc(18px + 1vw);
  }

  margin-left: 15px;
}

.skillsItem {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.skillsIMG img {
  max-width: 64px;
  height: auto;
}

/*Media*/

@media (max-width: 1280px) {
  .skillsIMG img {
    max-width: 58px;
  }
}

@media (max-width: 1024px) {
  .skillsItem {
    width: 160px;
  }
}

@media (max-width: 768px) {
  .skillsIMG img {
    max-width: 54px;
  }

  .skillsText p {
    margin-top: 6px;
  }
}

@media (max-width: 610px) {
  .skillsItem {
    width: 150px;
  }
}