.aboutMeWrapper {
  display: grid;
  grid-template-areas: "photo aboutMe";
  grid-template-columns: 1fr 1fr;
  align-items: center;
  position: relative;
  margin-top: 330px;

  p {
    color: #ffffff;
  }
}

.photo {
  border-radius: 60px;

  img {
    border-radius: 60px;
    max-width: 100%;
    height: auto;
    display: block;
  }

  grid-area: photo;
  margin-top: 16px;
  margin-right: 180px;
}

.aboutMe {
  grid-area: aboutMe;
  justify-self: end;
  align-self: start;
  height: 100%;
  position: relative;

  &:before {
    content: "";
    position: absolute;
    bottom: 0;
    left: -9.4%;
    border-left: 1px #ffffff solid;
    height: calc(100% - 16px);
  }
}

.aboutMeTitle p {
  font-family: "TTCommons-DemiBold", sans-serif;
  font-size: calc(39px + 3vw);
  line-height: calc(63px + 3vw);
}

.aboutMeText {
  margin-top: 16px;

  p {
    font-family: "TTCommons-Regular", sans-serif;

    &:not(:nth-child(3)) {
      margin-bottom: 30px;
    }
  }

  span {
    font-family: "TTCommons-Medium", sans-serif;
  }

  p, span {
    font-size: calc(15px + 0.5vw);
    line-height: calc(17px + 0.5vw);
  }
}

/*Media*/

@media (max-width: 1024px) {
  .aboutMeWrapper {
    grid-template-areas: "photo" "aboutMe";
    grid-template-columns: 1fr;
    align-items: center;
    justify-items: center;
  }

  .photo {
    margin-top: 0;
    margin-right: 0;
    display: flex;
    justify-content: center;

    img {
      max-width: 80%;
    }
  }

  .aboutMe {
    margin-top: 60px;
    justify-self: center;
    align-self: center;
    height: auto;

    &:before {
      content: none;
    }
  }
}

@media (max-width: 1280px) {
  .aboutMeWrapper {
    margin-top: 260px;
  }
}

@media (max-width: 1024px) {
  .aboutMeWrapper {
    margin-top: 200px;
  }

  .aboutMeTitle {
    text-align: center;
  }
}

@media (max-width: 768px) {
  .aboutMeWrapper {
    margin-top: 160px;
  }
}

@media (max-width: 425px) {
  .aboutMeWrapper {
    margin-top: 130px;
  }

  .aboutMeText p {
    text-align: justify;
  }
}