.menu {
  ul {
    display: flex;

    a {
      color: #ffffff;
    }
  }

  grid-area: menu;
  justify-self: end;

  li {
    &:not(:nth-child(5)) {
      margin-right: 34px;
    }

    a {
      font-family: "TTCommons-Light", sans-serif;
      font-size: calc(5px + 1vw);
      line-height: 28px;
      position: relative;

      &:after {
        content: "";
        display: block;
        position: absolute;
        bottom: -4px;
        width: 100%;
        border-bottom: 1px #ffffff solid;
        opacity: 0;
      }

      &:hover:after {
        transition: opacity ease 600ms;
        opacity: 100%;
      }
    }
  }
}

/*Media*/

@media (max-width: 768px) {
  .menu {
    position: absolute;
    right: 0;
    background: #252734;
    top: -30px;
    width: 260px;
    transform: translateY(-100%);
    transition: transform 200ms;

    ul {
      flex-direction: column;
      margin: 70px 0 35px 45px;
    }

    li {
      a {
        font-family: "TTCommons-Light", sans-serif;
        font-size: calc(12px + 1vw);
        line-height: 28px;
      }

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  .active.menu {
    transition: transform 200ms;
    transform: translateY(0%);
  }
}

@media (max-width: 320px) {
  .menu {
    width: 100%;
  }
}